body.pasteleria-canina-y-felina {

    section#first-scroll {
        /* Background moved to critical CSS in header for faster LCP */
        background-position: center;
    }

    img.icon {
        left: -150px;
        top: 47px;
        transform: rotate(-46deg);
        width:120px;

        &.last {
            right: -150px;
            top: 47px;
            transform: rotate(46deg);
            left: unset;
        }
    }
}

/* Prevent CLS - Responsive image containers */
.img-rounded, .lazyload, img[data-src] {
    aspect-ratio: 16/9;
    width: 100%;
    height: auto;
    object-fit: cover;
    
    /* Fallback for older browsers */
    @supports not (aspect-ratio: 16/9) {
        &::before {
            content: "";
            display: block;
            padding-top: 56.25%; /* 16:9 aspect ratio */
        }
    }
}

/* Specific aspect ratios for different image types */
.muestras-alumnas img {
    aspect-ratio: 1/1; /* Square images */
}

.opiniones-alumnas img {
    aspect-ratio: 4/3; /* Portrait images */
}

/* Fix touch targets for accessibility */
@media (max-width: 768px) {
    a, button, [role="button"] {
        min-height: 44px;
        min-width: 44px;
        padding: 8px 12px;
        margin: 4px;
        display: inline-block;
        line-height: 1.4;
    }
    
    /* Social media links */
    a[href*="facebook.com"], 
    a[href*="instagram.com"],
    a[href*="contacto"] {
        min-height: 48px;
        min-width: 48px;
        padding: 12px;
        margin: 8px 4px;
        border-radius: 4px;
        background-color: rgba(255,255,255,0.1);
        transition: background-color 0.2s;
    }
    
    a[href*="facebook.com"]:hover,
    a[href*="instagram.com"]:hover,
    a[href*="contacto"]:hover {
        background-color: rgba(255,255,255,0.2);
    }
}

/* Fix color contrast for accessibility */
.buttonCallToAction, 
.btn-primary,
button[class*="button"],
a[class*="button"],
.certificate-heading {
    background-color: #1a5f6b !important; /* Darker background */
    color: #ffffff !important; /* White text */
    border: 2px solid #ffffff !important;
    font-weight: 600 !important;
    text-shadow: 1px 1px 2px rgba(0,0,0,0.5) !important;
}

.buttonCallToAction:hover,
.btn-primary:hover,
button[class*="button"]:hover,
a[class*="button"]:hover {
    background-color: #0d4248 !important; /* Even darker on hover */
    color: #ffffff !important;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0,0,0,0.3) !important;
}

/* Improve text contrast in certificates and badges */
.certificate-heading,
[itemprop="name"],
h2, h3, h4 {
    color: #1a1a1a !important;
    text-shadow: 1px 1px 2px rgba(255,255,255,0.8) !important;
}

/* Ensure good contrast for duration elements */
[itemscope*="Duration"] span,
.days, .seconds {
    background-color: rgba(26, 95, 107, 0.9) !important;
    color: #ffffff !important;
    padding: 4px 8px !important;
    border-radius: 4px !important;
    font-weight: 600 !important;
}

